-top:-8rem;margin-right:3rem}a{text-decoration:none;cursor:pointer}.not-published{cursor:no-drop}.not-published__overlay{position:absolute;top:0;right:0;width:100%;height:100%;font-size:21px;display:flex;justify-content:center;align-items:center;color:white;text-align:center;background-color:transparent;transition:background-color 0.5s}.not-published__overlay:hover,.not-published__overlay:focus{background-color:rgba(0,0,0,0.7)}.app{width:100%;background:white}.app__bg{width:100%;position:absolute;top:15%;right:0;z-index:1}.app .main{display:flex;flex-direction:row}.app .headline__text{color:#000;font-size:70px;cursor:default}.app .headline__subtext{color:#000;padding:1rem 0}

