  
@import "variables.scss";  
@import "base.scss";
@import "typography.scss";
@import "buttons.scss";

@import "logo.scss";
@import "header.scss";
@import "home.scss";
@import "projects.scss";
@import "blog.scss";
a{
    text-decoration: none;
    cursor: pointer;
}
.not-published{
    cursor: no-drop;
    &__overlay{
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        font-size: 21px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        text-align: center;
        background-color: transparent;
        transition: background-color 0.5s;
        &:hover,&:focus{
        background-color: rgba($color: #000000, $alpha: 0.7);
        }
    }
}
.app{
    width: 100%;
    background: white;
    &__bg{
        width: 100%;
        position: absolute;
        top: 15%;
        right: 0;
        z-index:1;
       }
       .main{
        @include d-flex-row;
       }
       .headline{
        &__text{
            color: $black;
            font-size: 70px;
            cursor: default;
        }
        &__subtext{
            color: $black;
            padding: 1rem 0;
        }
        }
}